        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" label="办理状态" width="120" property="manage_status_name">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" label="司检业务日期" width="120" property="appraisal_business_time_date">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" label="司赔业务日期" width="120" property="claim_business_time_date">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" label="司检单号" width="180" property="company_appraisal_sn">
          <template slot-scope="scope">
            <el-button @click="seeDetail(scope,scope.row)" type="text" size="small">
              {{scope.row.company_appraisal_sn}}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" label="司赔单号" width="180" property="company_claim_sn">
        </el-table-column>
