      <div class="table_sum" ref='table_sum' v-show="!!desc">
        <ul style="overflow:auto;width:11111px;">
          <li style="width:746px;text-align:left;padding-left:52px;">
            总计
          </li>
          <li style="width:119px;text-align:left">
            {{this.TableData.allList.all_num}}</li>
          <li style="width:242px;padding-left:101px">
            {{this.TableData.allList.all_surplus_flowerdeep}}</li>
          <li style="width:139px">
            {{this.TableData.allList.all_abrasion_price}}</li>
