</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "aftersaleGetTireListIndex",
  data() {
    return {
      TableData: {
        tableData: [],
        allList: {},
        total: 10
      },
      departments: {
        options: []
      },
      desc: [], //合计信息
      goods_name: [],
      loading:false,
      SelesTomer: {
        options: []
      }, //客户列表
      params: {
        manage_status: 0
      }, // 数据列表
      importDialog: false,
      headersToken: {
        token: localStorage.token
      }
    };
  },
  computed: {
    ...mapGetters(["getTireListIndex_Get"])
  },
  methods: {
    ...mapActions(["getTireListSeeACT", "getTireListIndexACT"]),
    employeeChange(val) {
      if (val) {
        this.params.employee_name = this.employeeList.filter(
          obj => obj.employee_id === val
        )[0].employee_name;
      }
    },
    goodsChange(val) {
      if (val) {
        this.params.goods_name = this.goods_name.filter(
          obj => obj.goods_id === val
        )[0].goods_name;
      }
    },
    customerChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    exportOrder(scheduleType) {
      // 导出胎号查询表URL
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportTurnUrl(this.params).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    seeDetail(scope, row) {
      let elTable1 = this.$refs.table1.$el.getElementsByClassName(
        "el-table__body-wrapper"
      )[0];
      this.getTireListSeeACT({
        commont: "getTireListSee",
        company_appraisal_goods_id: row.company_appraisal_goods_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize,
        scrollLeft: elTable1.scrollLeft,
        scrollTop: elTable1.scrollTop
      });
    },
    getSummaries(param) {
      let _this = this;
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "页合计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 8) {
            sums[index] = sums[index].toFixed(2);
          } else if (index === 9) {
            sums[index] = sums[index];
          } else if (index === 10) {
            sums[index] = sums[index];
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }

        if (column.property === "tire_un") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = this.TableData.tableData.length;
        }
      });

      return sums;
    },
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中值
      this.$set(this.params, "employee_id", ""); // 初始化质检员下拉选中值
    },
    onReset() {
      this.$set(this, "params", {
        aftersales_status: 0,
        manage_status: 0
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getTrunList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams, 2);
      // wz cache
      this.getTireListIndexACT({
        params: {},
        goPage: this.goPage,
        pageSize: this.pageSize,
        commont: "getTireListIndex" // 为getTireList 组件中得watch监控添加组件关键字
      });
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }

      Object.assign(this.searchParams.params, this.params, {
        page: this.goPage
      });

      this.searchParams.commont = "getTireListIndex";
      getData(this.searchParams, 2);
      // wz cache
      this.getTireListIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize,
        commont: "getTireListIndex" // 为getTireList 组件中得watch监控添加组件关键字
      });
    }
  },
  mounted() {
    // wz cache
    let wz_search_params = {};
    if (this.getTireListIndex_Get && this.getTireListIndex_Get.params) {
      this.$set(this, "params", this.getTireListIndex_Get.params);
      wz_search_params = this.params; // wz 赋值查询参数    // wz cache

      // wz 单独处理 商品名称
      this.goods_name.push({
        goods_id: this.params.goods_id,
        goods_name: this.params.goods_name
      });
      opthionList(this, this.getTireListIndex_Get.params);
    }
    if (this.getTireListIndex_Get && this.getTireListIndex_Get.goPage) {
      this.params.goPage = this.getTireListIndex_Get.goPage;
      this.params.pageSize = this.getTireListIndex_Get.pageSize;
      this.getTireListIndex_Get.params &&
        this.$set(this, "params", this.getTireListIndex_Get.params);
    }
    this.$set(this, "searchParams", {
      params: wz_search_params,
      TableData: this.TableData,
      API: "getTrunList",
      vm: this
    });
    getData(this.searchParams, 2); //获取胎号查询列表
    let elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    if (this.getTireListIndex_Get && this.getTireListIndex_Get.scrollLeft) {
      //vuex传来的滚动条距离
      this.$nextTick(() => {
        elTable1.scrollTop = this.getTireListIndex_Get.scrollTop;
        // elTable1.scrollLeft = this.getTireListIndex_Get.scrollLeft;
      });
    }
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function() {
      elTable2.scrollLeft = this.scrollLeft;
    });
  }
};
</script>
