<template>
  <div>
    <component v-bind:is="commont"></component>
  </div>
</template>

<script>
import getTireListIndex from "./getTireListIndex.vue";
import getTireListSee from "./getTireListSee.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  components: {
    getTireListIndex,
    getTireListSee
  },
  data() {
    return {
      commont: "getTireListIndex"
    };
  },
  computed: {
    ...mapGetters(["getTireListIndex_Get", "getTireListSee_Get"])
  },
  watch: {
    getTireListIndex_Get() {
      this.commont = this.getTireListIndex_Get.commont;
    },
    getTireListSee_Get() {
      this.commont = this.getTireListSee_Get.commont;
    }
  }
};
</script>

