<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "aftersaleGetTireListSee",
  data() {
    return {
      authority: {},
      options4: [], //病象管理
      table: {}
    };
  },
  methods: {
    ...mapActions(["getTireListIndexACT"]),
    goBack() {
      // wz annotation cache
      this.getTireListIndexACT({
        commont: "getTireListIndex",
        // params: this.getTireListSee_Get.params,
        params: this.getTireListIndex_Get
          ? this.getTireListIndex_Get.params
          : {},
        goPage: this.getTireListSee_Get.goPage,
        pageSize: this.getTireListSee_Get.pageSize,
        scrollLeft: this.getTireListSee_Get.scrollLeft,
        scrollTop: this.getTireListSee_Get.scrollTop
      });
    },
    audit(api) {
      //审核和作废
      this.$axMethod[api]({
        company_appraisal_goods_id: this.companyExamine_Get
          .company_appraisal_goods_id
      }).then(response => {
        if (response.data.code == 200) {
          this.$message({
            message: response.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    }
  },
  computed: {
    ...mapGetters(["getTireListSee_Get", "getTireListIndex_Get"])
    // getNEwlist() {
    //   let vm = this;
    //   this.$axMethod.getDiseaseList().then(response => {
    //     let goodsList = response.data.data;
    //     // this.options4 = goodsList;
    //     vm.$set(vm, "options4", goodsList);
    //   }); //病象管理下拉搜索
    // }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    let sisal = this.getTireListSee_Get.company_appraisal_goods_id;
    this.$axMethod
      .infoCompanyAppraisal({
        company_appraisal_goods_id: sisal
      })
      .then(response => {
        this.table = response.data.data;
      });
  }
};
</script>

